import React from "react";
import "./Sidebar.css";

import { Button, IconButton } from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import InboxIcon from "@mui/icons-material/Inbox";
import SidebarOptions from "./SidebarOptions";
import StarBorderIcon from "@mui/icons-material/StarBorder";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import LabelOutlinedIcon from "@mui/icons-material/LabelOutlined";
import SendOutlinedIcon from "@mui/icons-material/SendOutlined";
import InsertDriveFileOutlinedIcon from "@mui/icons-material/InsertDriveFileOutlined";
import LabelIcon from "@mui/icons-material/Label";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import AddOutlinedIcon from "@mui/icons-material/AddOutlined";

function Sidebar() {
  return (
    <div className="sidebar">
      <Button
        startIcon={<EditIcon fontSize="medium" className="sidebar__compose" />}
        className="sidebar__button"
      >
        Compose
      </Button>

      <div className="sidebar__bodymenu">
        <SidebarOptions
          Icon={InboxIcon}
          title="Inbox"
          number="546"
          selected={false}
        />
        <SidebarOptions
          Icon={StarBorderIcon}
          title="Starred"
          number="3"
          selected={true}
        />
        <SidebarOptions Icon={AccessTimeIcon} title="Snoozed" number="8" />
        <SidebarOptions Icon={LabelOutlinedIcon} title="Important" number="1" />
        <SidebarOptions Icon={SendOutlinedIcon} title="Sent" number="19" />
        <SidebarOptions
          Icon={InsertDriveFileOutlinedIcon}
          title="Draft"
          number=""
        />
        <SidebarOptions Icon={LabelIcon} title="Categories" />
        <SidebarOptions Icon={KeyboardArrowDownIcon} title="More" />
      </div>
      <div className="sidebar__footerlabels">
        <h5>Labels</h5>
        <IconButton className="label__icon">
          <AddOutlinedIcon />
        </IconButton>
      </div>
    </div>
  );
}

export default Sidebar;
