import { StarBorderOutlined } from "@mui/icons-material";
import LabelOutlinedIcon from "@mui/icons-material/LabelOutlined";
import { Checkbox, IconButton } from "@mui/material";
import React from "react";
import "./EmailRow.css";

function EmailRow({ id, title, subject, description, time }) {
  return (
    <div className="email__row">
      <div className="row__left">
        <div className="email__rowButtons">
          <Checkbox className="row__iconButton" size="small" />

          <StarBorderOutlined className="row__iconButton " />

          <LabelOutlinedIcon className="row__iconButton star__fav" />
        </div>
        <h3 className="email__rowTitle">{title}</h3>

        <div className="email__rowSubjectDesc">
          <h3>{subject}</h3>
          <p>
            {description.length > 40
              ? `${description.split(" ").slice(0, 16).join(" ")}...`
              : `${description.split(" ").slice(0, 10).join(" ")}...`}
          </p>
        </div>
      </div>
      <div className="email__rowtime">
        <p>{time}</p>
      </div>
    </div>
  );
}

export default EmailRow;
