import React from "react";
import "./SectionIcon.css";

function SectionIcon({ Icon, title, highlight, badge, color, selected, id }) {
  return (
    <div className={`section__wrapper ${selected && "item--selected"}`} id={id}>
      <Icon className="section__iconItem" />
      <div className="section__content">
        <div className="section__titlenBadge">
          <p>{title}</p>
          <h5
            style={{
              backgroundColor: color,
              color: "white",
              paddingLeft: "4px",
              paddingRight: "4px",
              borderRadius: "15px",
            }}
          >
            {badge}
          </h5>
        </div>
        <p>{highlight}</p>
      </div>
    </div>
  );
}

export default SectionIcon;
