import React from "react";
import "./Header.css";
import MenuIcon from "@mui/icons-material/Menu";
import TuneIcon from "@mui/icons-material/Tune";
import HelpOutlineIcon from "@mui/icons-material/HelpOutline";
import SettingsIcon from "@mui/icons-material/Settings";
import AppsRoundedIcon from "@mui/icons-material/AppsRounded";
import SearchIcon from "@mui/icons-material/Search";
import { Avatar, IconButton } from "@mui/material";
import Duke from "../src/assets/Duke_profile.png";

function Header() {
  return (
    <div className="header">
      <div className="header__left">
        <IconButton>
          <MenuIcon />
        </IconButton>
        <img
          src="https://logodownload.org/wp-content/uploads/2018/03/gmail-logo-1-1.png"
          alt="/"
        />
      </div>
      <div className="header__middle">
        <div className="input__left">
          <IconButton>
            <SearchIcon />
          </IconButton>
          <input type="search" placeholder="Search in mail" />
        </div>

        <IconButton>
          <TuneIcon />
        </IconButton>
      </div>
      <div className="header__right">
        <IconButton>
          <HelpOutlineIcon />
        </IconButton>
        <IconButton>
          <SettingsIcon />
        </IconButton>
        <IconButton>
          <AppsRoundedIcon />
        </IconButton>
        <IconButton>
          <Avatar sx={{ width: 34, height: 34 }} src={Duke} />
        </IconButton>
      </div>
    </div>
  );
}

export default Header;
